#hamburger-icon {
  width: 32px;
  height: 22px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  text-align: right;
  z-index: 6;
  right: 15px;
  top: 30px;
  transition: top 0.3s ease-in-out;
}
#hamburger-icon.active .line {
  background: black;
}
#hamburger-icon .line {
  display: block;
  background: white;
  width: 32px;
  height: 2px;
  right: 0;
  border-radius: 1px;
  transition: all 0.4s;
}
#hamburger-icon .line.line-1 {
  top: 0;
}
#hamburger-icon .line.line-2 {
  top: 50%;
}
#hamburger-icon .line.line-3 {
  top: 100%;
}
#hamburger-icon.active .line {
  background: white;
  color: white;
}
#hamburger-icon.active .line-1 {
  transform: translateY(11px) translateX(0) rotate(45deg);
  width: 100%;
}
#hamburger-icon.active .line-2 {
  opacity: 0;
  width: 100%;
}
#hamburger-icon.active .line-3 {
  transform: translateY(-9px) translateX(0) rotate(-45deg);
  width: 100%;
}

.mfp-bg {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2000;
  overflow: hidden;
  position: fixed;
  background: #0b0b0b;
  opacity: 0.8;
