.animated {
  transition: opacity 1.2s, transform 1.2s;
}
.animated.to-right {
  transform: translateX(-200px);
  opacity: 0;
}
.animated.to-left {
  transform: translateX(200px);
  opacity: 0;
}
.animated.to-top {
  transform: translateY(300px);
  opacity: 0;
}
.animated.to-bottom {
  transform: translateY(-200px);
  opacity: 0;
}
.animated.fade-in {
  opacity: 0;
}
.animated.perspective {
  transform-origin: 485px 64px;
  transform: rotateX(90deg);
  animation: perspectiveAnimate 1s linear;
}

@keyframes perspectiveAnimate {
  0% {
    opacity: 0;
    transform: rotateX(90deg);
  }
  100% {
    opacity: 1;
    transform: rotateX(0deg);
  }
}
@keyframes to-left {
  0% {
    opacity: 0;
    transform: translateX(150px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes to-right {
  0% {
    opacity: 0;
    transform: translateX(-150px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes to-top {
  0% {
    opacity: 0;
    transform: translateY(300px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes to-bottom {
  0% {
    opacity: 0;
    transform: translateY(-150px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.animated-sync-top {
  transform: translateY(200px);
  opacity: 0;
}

.animated-sync-right {
  transform: translateX(-200px);
  opacity: 0;
}

.sync-to-top {
  animation-name: to-top;
  animation-duration: 0.8s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: both;
}
@media (min-width: 768px) {
  .sync-to-top:nth-child(1) {
    animation-delay: 0.35s;
  }
  .sync-to-top:nth-child(2) {
    animation-delay: 0.6s;
  }
  .sync-to-top:nth-child(3) {
    animation-delay: 0.85s;
  }
  .sync-to-top:nth-child(4) {
    animation-delay: 1.1s;
  }
}
@media (max-width: 767px) {
  .sync-to-top:nth-child(1) {
    animation-delay: 0.2s;
  }
  .sync-to-top:nth-child(2) {
    animation-delay: 0.3s;
  }
  .sync-to-top:nth-child(3) {
    animation-delay: 0.4s;
  }
  .sync-to-top:nth-child(4) {
    animation-delay: 0.5s;
  }
}

.sync-to-right {
  animation-name: to-right;
  animation-duration: 0.8s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: both;
}
@media (min-width: 768px) {
  .sync-to-right:nth-child(1) {
    animation-delay: 0.1833333333s;
  }
  .sync-to-right:nth-child(2) {
    animation-delay: 0.2666666667s;
  }
  .sync-to-right:nth-child(3) {
    animation-delay: 0.35s;
  }
}
@media (max-width: 767px) {
  .sync-to-right:nth-child(1) {
    animation-delay: 0.2s;
  }
  .sync-to-right:nth-child(2) {
    animation-delay: 0.3s;
  }
  .sync-to-right:nth-child(3) {
    animation-delay: 0.4s;
  }
}

